.card{
  width: 280px;
  display: flex;
  flex-direction: column;
  gap: 10px;

  .image{
    width: 100%;
    height: 400px;
    overflow: hidden;
    position: relative;

    &:hover{
      .secondImg{
        z-index: 2;
      } 
    }

    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
    }

    .mainImg{
      z-index: 1;
    }

    span{
      position: absolute;
      top: 5px;
      left: 5px;
      background-color: #fff;
      color: #f50505;
      padding: 3px 5px;
      z-index: 3;
      font-size: 12px;
      letter-spacing: 1px;
      font-weight: 600;
    }
  }

  h2{
    font-size: 20px;
    font-weight: 200;
  }

  .prices{
    display: flex;
    gap: 15px;
    margin-top: -30px;

    h3{
      font-size: 16px;

      &:first-child{
        color: #868686;
        text-decoration: line-through;
      }
    }
  }
}